import React, { useEffect, useState } from "react";
import axios from "axios";
import "./RowPost.css";
import { API_KEY, IMAGE_URL, baseUrl } from "../../constatns/constant";
import YouTube from "react-youtube";

function RowPost(props) {
  const [movie, setMovie] = useState([]);
  const [moveieKey, setMovieKey] = useState();

  useEffect(() => {
    axios
      .get(props.url)
      .then((response) => {
        console.log(response.data.results[0]);
        setMovie(response.data.results);
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);
  const handleTrailer = (id) => {
    console.log(id);
    axios
      .get(`${baseUrl}/movie/${id}/videos?api_key=${API_KEY}&language=en-US`)
      .then((response) => {
        console.log(response.data.results);
        if (response.data.length !== 0) {
            setMovieKey(response.data.results[0]);
        }
      });
  };
  
  const opts = {
    height: "390",
    width: "100%",
    playerVars: {
      autoplay: 1,
    },
  };
  return (
    <div className="row">
      <h2>{props.title}</h2>
      <div className="posters">
        {movie.map((item) => (
          <div className="box" onClick={() => handleTrailer(item.id)}>
            <img
              src={`${IMAGE_URL + item.backdrop_path}`}
              alt="Movie-card"
              className={props.isSmall ? "small-box" : ""}
            />
            <div className="overview">
              <span>{props.active ? item.original_name : item.title}</span>
            </div>
          </div>
        ))}
      </div>
      { moveieKey && <YouTube videoId={moveieKey.key} opts={opts} /> }
    </div>
  );
}

export default RowPost;
